/* Perfect Government Login Page Styling */

/* Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.login-page {
  min-height: 100vh;
  background-color: var(--background-light);
  font-family: 'Inter', 'Roboto', sans-serif;
}

/* Loading State */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: var(--background-light);
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid var(--border-light);
  border-top: 4px solid var(--primary-brown);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-container p {
  color: var(--text-secondary);
  font-size: 1rem;
}

/* Government Header */
.govt-header {
  background: white;
  border-bottom: 1px solid var(--border-light);
  box-shadow: 0 2px 4px var(--shadow-light);
  position: sticky;
  top: 0;
  z-index: 100;
}

.header-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 12px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.govt-logo {
  display: flex;
  align-items: center;
  gap: 16px;
}

.emblem {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--banner-brown), var(--banner-dark));
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px var(--shadow-light);
}

.emblem span {
  font-size: 1.5rem;
}

.govt-text {
  display: flex;
  flex-direction: column;
}

.hindi-text {
  font-size: 0.8rem;
  color: var(--text-secondary);
  font-weight: 400;
}

.english-text {
  font-size: 1.1rem;
  color: var(--primary-brown);
  font-weight: 700;
  letter-spacing: 0.5px;
}

.header-icons {
  display: flex;
  gap: 16px;
}

.icon {
  font-size: 1.2rem;
  opacity: 0.7;
  cursor: pointer;
  transition: opacity 0.3s ease;
  color: var(--text-secondary);
}

.icon:hover {
  opacity: 1;
}

/* Main Banner */
.main-banner {
  background: linear-gradient(135deg, var(--banner-brown) 0%, var(--banner-dark) 100%);
  padding: 60px 20px;
  text-align: center;
  color: white;
  position: relative;
  overflow: hidden;
}

.main-banner::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 25% 25%, rgba(255,255,255,0.1) 1px, transparent 1px),
              radial-gradient(circle at 75% 75%, rgba(255,255,255,0.08) 1px, transparent 1px);
  background-size: 50px 50px, 30px 30px;
  animation: float 20s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  33% { transform: translateY(-10px) rotate(1deg); }
  66% { transform: translateY(-5px) rotate(-1deg); }
}

.banner-content {
  position: relative;
  z-index: 2;
  max-width: 600px;
  margin: 0 auto;
}

.portal-title {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 12px;
  text-shadow: 0 4px 8px rgba(0,0,0,0.3);
  letter-spacing: -1px;
}

.portal-subtitle {
  font-size: 1.2rem;
  font-weight: 400;
  margin-bottom: 20px;
  opacity: 0.95;
}

.govt-emblem {
  font-size: 3rem;
  margin-top: 20px;
  opacity: 0.9;
}

/* Login Section */
.login-section {
  background: var(--background-light);
  padding: 60px 20px;
}

.login-container {
  max-width: 800px;
  margin: 0 auto;
}

.login-card {
  background: white;
  border-radius: 16px;
  box-shadow: 0 8px 32px var(--shadow-medium);
  overflow: hidden;
  border: 1px solid var(--border-light);
}

.card-header {
  background: linear-gradient(135deg, var(--banner-brown) 0%, var(--banner-dark) 100%);
  color: white;
  padding: 32px;
  text-align: center;
  border-bottom: 4px solid var(--accent-gold);
}

.card-header h2 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 8px;
}

.card-header p {
  font-size: 1rem;
  opacity: 0.9;
  margin: 0;
}

.card-body {
  padding: 40px;
}

/* Login Grid */
.login-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  margin-bottom: 32px;
}

.login-option {
  background: linear-gradient(135deg, #ffffff 0%, #fefefe 100%);
  border: 2px solid var(--border-light);
  border-radius: 12px;
  padding: 24px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 20px;
  position: relative;
  overflow: hidden;
}

.login-option::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--banner-brown), var(--accent-gold));
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}

.login-option:hover::before {
  transform: translateX(0);
}

.login-option:hover {
  background: linear-gradient(135deg, var(--background-cream) 0%, var(--background-beige) 100%);
  border-color: var(--primary-brown);
  transform: translateY(-4px);
  box-shadow: 0 8px 24px var(--shadow-medium);
}

.option-icon {
  font-size: 3rem;
  opacity: 0.8;
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.login-option:hover .option-icon {
  transform: scale(1.1);
  opacity: 1;
}

.option-info {
  flex: 1;
}

.option-title {
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 6px 0;
}

.option-subtitle {
  font-size: 1rem;
  color: var(--text-secondary);
  margin: 0 0 16px 0;
  line-height: 1.3;
}

.user-info {
  background: rgba(139, 111, 71, 0.05);
  padding: 12px 16px;
  border-radius: 8px;
  border-left: 4px solid var(--primary-brown);
}

.user-info strong {
  display: block;
  color: var(--primary-brown);
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 6px;
}

.user-info .detail {
  display: block;
  font-size: 0.85rem;
  color: var(--text-secondary);
  margin-bottom: 2px;
}

.option-arrow {
  font-size: 1.8rem;
  color: var(--primary-brown);
  opacity: 0.6;
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.login-option:hover .option-arrow {
  opacity: 1;
  transform: translateX(6px);
}

/* Info Notice */
.info-notice {
  background: linear-gradient(135deg, var(--background-cream) 0%, var(--background-beige) 100%);
  border: 1px solid var(--accent-gold);
  border-left: 4px solid var(--banner-brown);
  border-radius: 8px;
  padding: 20px;
  margin-top: 24px;
}

.notice-content {
  display: flex;
  align-items: flex-start;
  gap: 16px;
}

.notice-icon {
  font-size: 1.4rem;
  flex-shrink: 0;
  margin-top: 2px;
}

.notice-content div {
  font-size: 0.9rem;
  line-height: 1.5;
  color: var(--text-secondary);
}

.notice-content strong {
  color: var(--primary-brown);
  font-weight: 600;
}

.notice-content code {
  background: var(--primary-brown);
  color: white;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 500;
}

/* Footer */
.page-footer {
  background: var(--banner-dark);
  color: rgba(255,255,255,0.9);
  padding: 32px 20px;
  text-align: center;
}

.page-footer p {
  font-size: 0.9rem;
  margin-bottom: 12px;
}

.security-info {
  font-size: 0.85rem;
  opacity: 0.8;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .portal-title {
    font-size: 2.5rem;
  }
  
  .portal-subtitle {
    font-size: 1rem;
  }
  
  .card-header {
    padding: 24px 20px;
  }
  
  .card-header h2 {
    font-size: 1.5rem;
  }
  
  .card-body {
    padding: 24px 20px;
  }
  
  .login-option {
    padding: 20px;
    gap: 16px;
  }
  
  .option-icon {
    font-size: 2.5rem;
  }
  
  .option-title {
    font-size: 1.2rem;
  }
  
  .option-subtitle {
    font-size: 0.9rem;
  }
  
  .header-container {
    padding: 8px 16px;
  }
  
  .govt-text {
    display: none;
  }
}

@media (max-width: 480px) {
  .login-option {
    flex-direction: column;
    text-align: center;
    gap: 12px;
  }
  
  .option-arrow {
    transform: rotate(90deg);
  }
  
  .login-option:hover .option-arrow {
    transform: rotate(90deg) translateX(6px);
  }
}